import * as d3 from 'd3';
function getColor(ind){
	return ['red','green','blue','yellow','orange'][ind];
}
export const drawChart = function(data){
	 d3.select('svg').remove();
	const container = d3.select('#chart')
	.append('svg')
	.attr('width',600)
	.attr('height',600);

	const lineGenerator = d3.line();
	const points = [
  [0, 80],
  [100, 100],
  [200, 30],
  [300, 50],
  [400, 40],
  [500, 80]
];

const xAxesLinesPoints = [
[[0,100],[600,100]],
[[0,200],[600,200]],
[[0,300],[600,300]],
[[0,400],[600,400]],
[[0,500],[600,500]],
]

xAxesLinesPoints.forEach((n,ind)=>{
	container
	.selectAll('.text')
	.data(n)
	.enter()
	.append('text')
	.text((d,i)=>{
		let text = ''
		if(ind == 0){
			if(i==0){
				text = '$0'
			}
			else{
				text = '$850bn'
			}
		}
		else if( ind == 1){
			if(i==0){
				text = '>100'
			}
			else{
				text = '4'
			}
		}
		else if( ind == 2){
			if(i==0){
				text = '<0'
			}
			else{
				text = '80%'
			}
		}
		else if( ind == 3){
			if(i==0){
				text = '<0%'
			}
			else{
				text = '58%'
			}
		}
		else if( ind == 4){
			if(i==0){
				text = '<$0'
			}
			else{
				text = '$120bn'
			}
		}
		return text;
	})
	.attr('x',d=>{if(d[0]==600){return 530}})
	.attr('y',d=>d[1]-10)
})

const pathData = lineGenerator(points);
const linesPoints = data.map(n=>{
	return {
		points:[
		[600/850 * n.data.revenues, 100],
		[596 - 600/96*n.data.pEratio,200],
		[600/80*n.data.grossMargin,300],
		[600/58*n.data.netMargin,400],
		[600/120*n.data.netIncome,500]
		],
		name:n.name
	};
})
container
.append('g')
.attr('id','xAxes');
const xAxesLines = xAxesLinesPoints.map(n=>lineGenerator(n)).forEach((n)=>{
	container
	.select('g')
	.append('path')
	.style('stroke','#616161')
	.attr('class','line')
	 .attr('d', n);	
	}
);

//draw lines
linesPoints.forEach((n,ind)=>{
	let linePath = lineGenerator(n.points);
	const group = container
	.append('g');
	group
	.append('path')
	.style('stroke',()=>getColor(ind))
	.attr('class','line')
	.attr('d',linePath);
})

//draw circle
linesPoints.forEach((n,ind)=>{
	container.selectAll('.circle')
	.data(n.points)
	.enter()
	.append('circle')
	.attr('cx',(d)=>d[0])
	.attr('cy',(d)=>d[1])
	.attr('r',20)
	.attr('fill',()=>getColor(ind))
})	
}
