<template>
  <div id="app">
     <div class="container">
      <h4 class="heading">Companies</h4>
      <div id="companies-container">
        <div v-for="company in companies" class="company" :key="company.id">
          <input type="checkbox" @change="addOrRemoveCompany(company,$event)"/>
          <img :src="company.icon" :alt="company.name"/>
          <span> {{company.name}}</span>
        </div>
      </div>
     </div>
     <div class="container">
       <h4 class="heading">Fundamental Matrix</h4>
       <div class="chart-wrapper">
       <div class="chart-labels">
         <h6>Revenues</h6>
         <h6>P/E Ratio</h6>
         <h6>Gross MArgin</h6>
         <h6>Net Margin</h6>
         <h6>Net Income</h6>
       </div>
       <div id="chart"></div>
      </div>
     </div>
  </div>
</template>

<script>
import {drawChart} from './services/chart.js'

export default {
  name: 'app',
  data:()=>({
    companies:[
    {id:1,name:'MSFT',data:{revenues:200,pEratio:15,grossMargin:70,netMargin:12,netIncome:40},icon:'http://localhost:8081/images/msft.png'},
    {id:2,name:'COUP',data:{revenues:400,pEratio:50,grossMargin:30,netMargin:20,netIncome:60},icon:'http://localhost:8081/images/coup.png'},
   {id:3,name:'RVN',data:{revenues:600,pEratio:50,grossMargin:40,netMargin:30,netIncome:80},icon:'http://localhost:8081/images/rvn.png'},
   {id:4,name:'GOOGL',data:{revenues:150,pEratio:70,grossMargin:10,netMargin:40,netIncome:10},icon:'http://localhost:8081/images/googl.png'},
   {id:5,name:'LPSN',data:{revenues:700,pEratio:10,grossMargin:50,netMargin:20,netIncome:50},icon:'http://localhost:8081/images/lpsn.png'},
   {id:6,name:'WDAY',data:{revenues:500,pEratio:30,grossMargin:40,netMargin:10,netIncome:20},icon:'http://localhost:8081//images/wday.png'},
   {id:7,name:'AMZN',data:{revenues:200,pEratio:50,grossMargin:60,netMargin:25,netIncome:50},icon:'http://localhost:8081/images/amzn.png'},
   {id:8,name:'NOW',data:{revenues:300,pEratio:40,grossMargin:20,netMargin:40,netIncome:80},icon:'http://localhost:8081/images/now.png'},
   {id:9,name:'ESTC',data:{revenues:400,pEratio:80,grossMargin:30,netMargin:50,netIncome:120},icon:'http://localhost:8081/images/estc.png'},
    ],
    selectedCompanies:[]
  }),
  mounted(){
    drawChart(this.selectedCompanies);
  },
  methods:{
    addOrRemoveCompany(comp,event){
      const val = event.target.checked;
      if(val){
        this.selectedCompanies.push(comp)
      }
      else{
        let toRemoveInd = this.selectedCompanies.findIndex(n=>n.id == comp.id);
        this.selectedCompanies.splice(toRemoveInd,1);
      }
      drawChart(this.selectedCompanies);

    }
  }
}
</script>

<style>
@import './App.css';
</style>
